import React from "react";
import { graphql, Link } from "gatsby";
import Layout from "../components/layout";
import ShareTab from "../components/shared/share-tab";
import { Breadcrumbs } from "../components/shared/breadcrumbs";
import SEO from "../components/seo";
import { BgWrapper } from "../components/shared/components";
import CategoryDrawer from "../components/help/CategoryDrawer";

const breadcrumbs = [
  {
    text: 'Начало',
    link: '/',
  },
  {
    text: 'FAQ',
    link: '/help-center/',
  },
]

export default ({ data }) => {
  const post = data.post;
  const allPosts = data.allPosts.edges;
  let b = breadcrumbs.slice();
  if (b[b.length - 1].text !== post.frontmatter.title)
    b.push({ text: post.frontmatter.title });


  return (
    <BgWrapper 
      bg={data.bg.childImageSharp.fluid.src}
      position='top'
      >
      <Layout>
        <SEO
          title={post.frontmatter.title}
        />
        <div className='d-flex justify-content-center'>
          <div className='col-12 col-lg-11 col-xl-10 row my-4 d-flex justify-content-center' style={{ paddingTop: '12rem' }}>
              <div className='col-12 col-lg-4 p-0 m-0'>
                <CategoryDrawer
                  allPosts={allPosts}
                  activePost={post.fields.slug}
                  activePostTag={post.frontmatter.category}
                />
              </div>

              <div className='col-12 col-lg-8 p-0 m-0'>
                <Link to={'/help-center/'} className='link'><span className='icon back-blue-arrow mr-1'></span> Назад</Link>
                <h1 className='my-3'>{post.frontmatter.title}</h1>
                <Breadcrumbs items={b} />

                <img src={post.frontmatter.image.childImageSharp.fluid.src} className='img-fluid' alt={post.frontmatter.title} />
                <div className='mt-5' dangerouslySetInnerHTML={{ __html: post.html }} />
                <ShareTab path={post.fields.slug} />
              </div>
          </div>
        </div>
      </Layout>
    </BgWrapper>
  );
}

export const query = graphql`
  query ($slug: String!) {
    allPosts: allMarkdownRemark(filter: {frontmatter: {tag: {eq: "help"}}}) {
      edges {
        node {
          fields {
            slug
          }
          frontmatter {
            title
            category
          }
        }
      }
    }
    post: markdownRemark(fields: {slug: {eq: $slug}}) {
      html
      fields {
        slug
      }
      frontmatter {
        title
        category
        image {
          childImageSharp {
            fluid {
              src
            }
          }
        }
      }
    }
    bg: file(relativePath: {eq: "help/help-single-bg.png"}) {
      childImageSharp {
        fluid (maxWidth: 2000) {
          src
        }
      }
    }
  }
`;